@import '../scss/mixins';
@import '../scss/variables';

.negro {
background: rgb(0, 0, 0);
    .logo {
    width: 40px;
    }
    .nav-item:hover {
    background: $hover-enlaces;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    }
    .nav-link {
    color: $enlaces;
    @include a(1rem, 200, 'Barlow');
    text-align: center;
    }
    .active {
    background: $activo-menu;
    }
}
/* AL HACER SCROLL */
.top-nav-collapse {
background: rgba(131, 131, 131, 0.9);
}